import { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import HttpService from 'services/httpService';
import { Grid, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Box, Typography, Button } from '@mui/material';
import MainCard from 'ui-component/cards/MainCard';
import { notifier } from 'react-toast-notifier';
import FileCopyOutlinedIcon from '@mui/icons-material/FileCopyOutlined';
import CancelOutlinedIcon from '@mui/icons-material/CancelOutlined';
import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import LocalShippingOutlinedIcon from '@mui/icons-material/LocalShippingOutlined';
import moment from 'moment';

const ViewOrderDetails = () => {
    const { orderId } = useParams();
    const navigate = useNavigate();
    const [orderView, setOrderView] = useState([]);
    const [orderProduct, setOrderProducts] = useState([]);
    const [orderTotal, setOrderTotal] = useState(0);
    const [shippingAddress, setShippingAddress] = useState({});
    useEffect(() => {
        // eslint-disable-next-line no-use-before-define
        getShippingOrderDetail(orderId);
        // eslint-disable-next-line
    }, []);

    const getShippingOrderDetail = async (orderId) => {
        try {
            let total = 0;
            const prodTotal = [];
            const result = await HttpService.getshippingOrderViewDetails(orderId);
            console.log(result.data, 'result');
            setOrderView(result.data);
            setOrderProducts(result.data.orderProducts);
            result.data.orderProducts.forEach((ele) => {
                prodTotal.push(ele.quantity * Number(ele.amount));
                total = prodTotal.reduce((a, b) => a + b, 0);
            });
            setOrderTotal(total);
            setShippingAddress(result.data.shippingAddress);
        } catch (error) {
            notifier({
                type: 'error',
                message: 'Get Customer Project Detail error',
                autoDismissTimeout: 5000
            });
        }
    };

    const handleStatusChange = (status) => {
        console.log('status', status);
    };

    const editShippingOrder = () => {
        console.log('edit order', orderId);
        navigate(`/edit-order/${orderId}`);
    };

    const cancelOrder = async () => {
        console.log('cancel order', orderId);
        try {
            await HttpService.cancelShippingOrder(orderId);
            navigate('/order-list');
            notifier({
                type: 'success',
                message: 'Order cancelled successfully',
                autoDismissTimeout: 5000
            });
        } catch (error) {
            notifier({
                type: 'error',
                message: 'Something went wrong!',
                autoDismissTimeout: 5000
            });
        }
    };

    const cloneOrder = async () => {
        console.log('clone order', orderId);
        try {
            await HttpService.cloneShippingOrder(orderId);
            navigate('/order-list');
            notifier({
                type: 'success',
                message: 'Order cloned successfully',
                autoDismissTimeout: 5000
            });
        } catch (error) {
            notifier({
                type: 'error',
                message: 'Something went wrong!',
                autoDismissTimeout: 5000
            });
        }
    };

    return (
        <>
            <MainCard title="Order Information">
                <Box sx={{ width: '100%' }}>
                    <Paper elevation={3} style={{ padding: 20, marginBottom: 20 }}>
                        <div style={{ marginBottom: 16, float: 'right' }}>
                            <Button variant="outlined" onClick={() => handleStatusChange('ship')} style={{ marginRight: 10 }}>
                                <LocalShippingOutlinedIcon style={{ marginRight: 10 }} /> Ship
                            </Button>
                            <Button variant="outlined" onClick={editShippingOrder} style={{ marginRight: 10 }}>
                                <EditOutlinedIcon style={{ marginRight: 10 }} /> Edit
                            </Button>
                            <Button variant="outlined" onClick={cancelOrder} style={{ marginRight: 10 }}>
                                <CancelOutlinedIcon style={{ marginRight: 10 }} /> Cancel
                            </Button>
                            <Button variant="outlined" onClick={cloneOrder} style={{ marginRight: 10 }}>
                                <FileCopyOutlinedIcon style={{ marginRight: 10 }} /> Clone
                            </Button>
                        </div>
                        <br />
                        <br />
                        <br />
                        <Grid style={{ display: 'flex', marginTop: '5%', marginBottom: '5%' }}>
                            <Grid item xs={3}>
                                <Typography variant="h4">Order Detail</Typography>
                                <Typography variant="h5" mt={1}>
                                    <ul style={{ listStyle: 'none', textAlign: 'left', paddingLeft: 0, marginTop: 'auto' }}>
                                        <li>
                                            <b>Order No.:</b> {orderView.orderId}
                                        </li>
                                        <li>
                                            <b>Date:</b> {moment(orderView.createdAt).format('MMM D, YYYY')}
                                        </li>
                                        <li>
                                            <b>Payment Type:</b> {orderView.orderType}
                                        </li>
                                        <li>
                                            <b>Order Weight:</b> {`${orderView.weightFd / 1000}kg`}
                                        </li>
                                        <li>
                                            <b>Dimension:</b> {orderView.dimension1} x {orderView.dimension2} x {orderView.dimension3}
                                        </li>
                                    </ul>
                                </Typography>
                            </Grid>
                            <Grid item xs={3}>
                                <Typography variant="h4">Warehouse Details</Typography>
                                <Typography variant="h5" mt={1}>
                                    {orderView.status === 'Ship' ? `Not Shipped` : 'Shipped'}
                                </Typography>
                            </Grid>
                            <Grid item xs={3}>
                                <Typography variant="h4">RTO Details</Typography>
                                <Typography variant="h5" mt={1}>
                                    {orderView.status === 'Ship' ? `Not Shipped` : 'Shipped'}
                                </Typography>
                            </Grid>
                            <Grid item xs={3}>
                                <Typography variant="h4">Customer Details</Typography>
                                <Typography variant="h5" mt={1}>
                                    {shippingAddress.firstName} {shippingAddress.lastName} {shippingAddress.city}, <br />{' '}
                                    {shippingAddress.state} {shippingAddress.picode} India, <br /> {shippingAddress.phoneno}
                                </Typography>
                            </Grid>
                        </Grid>

                        <Grid>
                            <TableContainer component={Paper}>
                                <Table aria-label="customized table">
                                    <TableHead>
                                        <TableRow>
                                            <TableCell align="center">
                                                <b>Product</b>
                                            </TableCell>
                                            <TableCell align="center">
                                                <b>Quantity</b>
                                            </TableCell>
                                            <TableCell align="center">
                                                <b>Product SKU</b>
                                            </TableCell>
                                            <TableCell align="center">
                                                <b>Product Total</b>
                                            </TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {orderProduct.map((row, i) => (
                                            <TableRow key={i + 1}>
                                                <TableCell component="th" scope="row" align="center">
                                                    {row.product}
                                                </TableCell>
                                                <TableCell align="center">{row.quantity}</TableCell>
                                                <TableCell align="center">{row.skucode}</TableCell>
                                                <TableCell align="center">{row.amount}</TableCell>
                                            </TableRow>
                                        ))}
                                        <TableRow key="4">
                                            <TableCell colSpan={3} align="right" style={{ fontWeight: 600 }}>
                                                Grand Total
                                            </TableCell>
                                            <TableCell align="center" style={{ fontWeight: 600 }}>
                                                Rs. {orderTotal}
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                </Table>
                            </TableContainer>
                        </Grid>
                    </Paper>
                </Box>
            </MainCard>
        </>
    );
};

export default ViewOrderDetails;
